@import url('https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i');

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background: #03a9f4;
  color: #333;
}

.author {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 1em;
  color: #fff;
}

.author a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
}

.author a:hover {
  color: #333;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
  height: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
}

.box {
  border: 40px solid #fff;
  border-radius: 4px;
  resize: none;
  outline: none;
  background: #fff;
  width: 90%;
  height: 40%;
  overflow-x: hidden;
  -webkit-box-shadow: 0 0.1em 2em rgba(0, 0, 0, 0.15);
          box-shadow: 0 0.1em 2em rgba(0, 0, 0, 0.15);
}

@media (min-width: 375px) {
  .box {
    width: 70%;
  }
}

@media (min-width: 768px) {
  .container {
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .box {
    width: 40%;
    height: 70%;
  }
}



